/**
 *  Проверка на наличие стартовых значений / Function assess the existance of need values
 */

const firstStart = () => {
    if(getScore() === undefined || parseInt(getScore()) === 0 || !isNumber(parseInt(getScore()))) { 
        localStorage.setItem('score', 0) 
    };

    if(getItems() === undefined || !isObject(JSON.parse(getItems()))) {
        localStorage.setItem('items', JSON.stringify({
            phone: 0,
            notebook: 0,
            pc: 0,
            office: 0,
            farm: 0,
            factory: 0,
            mine: 0,
            skyscrapper: 0,
            island: 0,
            country: 0,
            planet: 0,
            solarSystem: 0,
            cosmos: 0
        }));
    }
    if(getPrices() === undefined || !isObject(JSON.parse(getPrices()))) { 
        localStorage.setItem('prices', JSON.stringify({
            phone: { 
                price: 100,
                boost: 2 
            },
            notebook: {
                price: 500,
                boost: 10
            },
            pc: {
                price: 2000,
                boost: 100
            },
            office: {
                price: 10000,
                boost: 500
            },
            farm: {
                price: 500000,
                boost: 2000
            },           
            factory: {
                price: 2000000,
                boost: 10000
            },            
            mine: {
                price: 10000000,
                boost: 100000
            },            
            skyscrapper: {
                price: 100000000,
                boost: 500000
            },            
            island: {
                price: 250000000,
                boost: 2500000
            },            
            country: {
                price: 1000000000,
                boost: 50000000
            },
            planet: {
                price: 5000000000,
                boost: 100000000
            },
            solarSystem: {
                price: 25000000000,
                boost: 500000000
            },
            cosmos: {
                price: 99999999999,
                boost: 9999999999
            }
        }));
    }    
    if(getData() === undefined || !isObject(JSON.parse(getData()))) { 
        localStorage.setItem('data', JSON.stringify({
            auto: 0,
            perClick: 1,
        }));
    }
    if(getCodes() === undefined || !isObject(JSON.parse(getCodes()))) {
        localStorage.setItem('codes',JSON.stringify([
            {
                value: '2019',
                hashes: 1000000,
                used: false
            },
            {
                value: 'sekki',
                hashes: 100000000,
                used: false,
            },
            {
                value: 'vkapp2019',
                hashes: 100000000,
                used: false
            }
        ]));
    }
}

