import React from 'react'
import {Panel, PanelHeader} from '@vkontakte/vkui';
import { Group, Div } from '@vkontakte/vkui';
import './style.css'
import { reset } from '../../core/main';

const Default = props => {

    const data = props.data;

    const add = () => {
        props.add(data.perClick);
    }

    const transform = () => {
        let score = JSON.stringify(props.score).split('');
        let length = score.length;
        const setLength = ( length ) => {
            let sum = '';
            for(let i = 0; i < length - 1; i++) {
            sum += score[i];
            }
            return sum;
        }
        if(length > 3) {
            if(length % 3 === 0) {
                if(length > 5) {
                return score[0] + score[1] + score[2] + '.' + score[3] + score[4] + score[5];
                }
                if(length === 5) {
                    return score[0] + score[1] + '.' + score[2] + score[3] + score[4];
                }
                if(length === 4) {
                    return score[0] + '.' + score[1] + score[2] + score[3];
                }
            } else {
                if(length > 5) {
                    return setLength(Math.floor(length / 3) * 3);
                }
            }
        }
        return score;
    }

    

    transform();

    return (
      <Panel id={props.id}>
        		<PanelHeader>
                    Clicker
		        </PanelHeader>
                <Group>
                        <Div className="score-container">
                            <p>{props.score}#</p>
                        </Div>    
                </Group>
                <Group>
                    <Div className="button-container">
                        <button className="button-click" onClick={() => add()}>
                            <h1>H+</h1>
                            <span>{data !== null ? data.auto : null} / сек</span>
                        </button>
                    </Div>
                </Group>
      </Panel>
    )
}
  
  export default Default
