import React, { useState } from 'react';
import {Panel, PanelHeader} from '@vkontakte/vkui';
import { Button, Group, Div, Input, FormLayoutGroup } from '@vkontakte/vkui';
import { promo } from '../../core/promo';

const Settings = props => {

    const [code, setCode] = useState('');

    const addBalance = () => {
        let hashes = promo(code).hashes;
        if(hashes !== undefined) {
            props.add(hashes);
        }
    }

    return (
      <Panel id={props.id}>
            <PanelHeader>
                Настройки
		    </PanelHeader>
        <Group>
            <Div>
            <FormLayoutGroup top="Промо-код">
                <Input type="text" defaultValue={code} onChange={(e) => setCode(e.target.value)} />
                <Button size="xl" onClick={() => addBalance()}>
                    Ввести промо-код
                </Button>
            </FormLayoutGroup>
            </Div>
            <Div>
                <button onClick={() => props.reset()}>
                       <h1>Сброс</h1>
                </button>
            </Div>
        </Group>
      </Panel>
    )
}

export default Settings;
