import React, { useState } from 'react';
import {Panel, PanelHeader, Group, Div } from '@vkontakte/vkui';
import './style.css';

const Shop = props => {	
	
	const [items, setItems] = useState(JSON.parse(localStorage.getItem('items')));
	const [prices, setPrices] = useState(JSON.parse(localStorage.getItem('prices')));
	console.log(prices);
	const [data, setData] = useState(JSON.parse(localStorage.getItem('data')));

	const buy = ( item ) => {
		if(props.score - prices[item].price >= 0) {
			props.add(-prices[item].price);
			setItems(Object.assign(items,{ [item]: items[item] + 1 }));
			setPrices(Object.assign(prices, { [item]: Object.assign(prices[item], { price: Math.round(prices[item].price * 1.1)})}));
			setData(Object.assign(data,{ auto: data.auto + prices[item].boost }));
			localStorage.setItem('items', JSON.stringify(items));
			localStorage.setItem('prices', JSON.stringify(prices));
			localStorage.setItem('data', JSON.stringify(data));
		}
	}

	const locale = {
		phone: 'Телефон',
		notebook: 'Ноутбук',
		pc: 'Компьютер',
		office: 'Офис',
		farm: 'Ферма',
		factory: 'Завод',
		mine: 'Шахта',
		skyscrapper: 'Небоскрёб',
		island: 'Остров',
		country: 'Страна',
		planet: 'Планета',
		solarSystem: 'Солнечная система',
		cosmos: 'Космос'
	}

	return(
		<Panel id={props.id}>
			<PanelHeader>
				Магазин
			</PanelHeader>
			<Group>
				<Div className="shop-score">
					{props.score}
				</Div>
			</Group>
			{Object.keys(items).map((item) => (

						<Group key={item}>
							<Div onClick={() => buy(item)} className="shop-item">
								<h4>{locale[item]}</h4> <p>Цена: { prices[item].price } | Кол-во: {items[item]} | +{ prices[item].boost }/с </p>             
							</Div>
						</Group>
					))
			}
		</Panel>
	);
}

export default Shop;
