import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, FixedLayout, Tabs, TabsItem } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Shop from './panels/Shop';
import Default from './panels/Default';
import Settings from './panels/Settings/';
import { firstStart, reset } from './core/main';

firstStart();

class App extends React.Component {
	constructor(props) {
		super(props);
		this.auto = undefined;
		this.state = {
			activePanel: 'default',
			fetchedUser: null,
			score: parseInt(localStorage.getItem('score')),
			data: JSON.parse(localStorage.getItem('data')),
			timer: false
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
			this.auto = setInterval(() => {
				this.add(this.state.data.auto);
			},1000);
	}

	componentWillUnmount() {
		return function cleanup() {
			clearInterval(this.auto);
		};
	}

	add = ( additional ) => {
    if(typeof score !== 'object') {
				localStorage.setItem('score', this.state.score + additional);
				this.setState({ data: JSON.parse(localStorage.getItem('data')), score: this.state.score + additional });
    }
	}
	
	fromScratch = () => {
		clearInterval(this.auto);
		this.setState({ score: 0, data: JSON.parse(localStorage.getItem('data')) });
		reset();
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<div>
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Shop id="shop" add={this.add} score={this.state.score} go={this.go} />
				<Default id="default" add={this.add} data={this.state.data} score={this.state.score} go={this.go} />
				<Settings id="settings" add={this.add} reset={this.fromScratch} score={this.state.score} go={this.go} />
			</View>
			<FixedLayout vertical="bottom">
              <Tabs>
                <TabsItem
                  onClick={(e) => this.go(e)}
                  data-to="default"
                  selected={this.state.activePanel === 'default'}
                >Главная</TabsItem>
                <TabsItem
                  onClick={(e) => this.go(e)}
                  data-to="shop"
                  selected={this.state.activePanel === 'shop'}
                >Магазин</TabsItem>
								<TabsItem
                  onClick={(e) => this.go(e)}
                  data-to="settings"
                  selected={this.state.activePanel === 'settings'}
                >Настройки</TabsItem>
              </Tabs>
            </FixedLayout>  
			</div>
		);
	}
}

export default App;
