const promo = ( code ) => {
    const codes = JSON.parse(localStorage.getItem('codes'));
    let validate = false;
    codes.map((item, index) => {
        if(item.value === code && item.used === false) {
            codes[index].used = true;
            console.log('here');
            localStorage.setItem('codes',JSON.stringify(codes));
            validate = item;
        }
    })
    return validate;
} 


export {
    promo
}